import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';

import { RegisterServiceProvider } from '../../providers/register-service/register-service';
import { LoadingServiceProvider } from '../../providers/loading-service/loading-service';
import { ToastServiceProvider } from '../../providers/toast-service/toast-service';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';

/**
 * Generated class for the OtpVerificationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-otp-verification',
  templateUrl: 'otp-verification.html',
})
export class OtpVerificationPage {

  otp: string = '';
  status: boolean = true;

  constructor(
  	public navCtrl: NavController,
    public navParams: NavParams,
    private registerService: RegisterServiceProvider,
    private toast: ToastServiceProvider,
    private alert: AlertServiceProvider,
    private loading: LoadingServiceProvider,
    private viewCtrl: ViewController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad OtpVerificationPage');
    this.viewCtrl.showBackButton(false);
  }

  verify(){
    let loader = this.loading.show('loading ....');
    loader.present().then(() => {
      this.registerService.verificationOtp(this.otp).subscribe((data) => this.verifySuccess(data,loader), error => this.verifyError(error,loader), () => this.saveDone());
    })
  }

  private saveDone(){
  }

  private verifyError(error,l){
  	console.log(error)
    l.dismiss().then(() => {
      if (error.status == 422) {
        this.alert.presentAlert(null,this.feedbackText(error.json()),'Close');
      } else if (error.status == 400) {
        this.alert.presentAlert(null,JSON.parse(error._body).error,'Close');
      } else {
        this.toast.show('Server Error');
      }
    });
  }

  private verifySuccess(data,l){
    l.dismiss().then(() => {
      this.navCtrl.setRoot('LoginPage');
    });
  }

  private feedbackText(error){
    let feedbackError = '<ion-list>';
    for(let key in error){
      for(let i = 0;i < error[key].length; i++){
        feedbackError += '<ion-item> - '+error[key][i]+'</ion-item><br><br>';
      }
    }
    feedbackError += '</ion-list>';

    return feedbackError;
  }

  onKey(event: any){
  	console.log('event',event.target.value.replace(/\D/g, '').length)

  	let length_otp = event.target.value.replace(/\D/g, '').length;

  	if(length_otp == 5){
  		this.status = false;
  	}

  	if(length_otp < 5){
  		this.status = true;
  	}

  }
}



